export default {
	data() {
		return {
			news:[]
		};
	},
	onLoad:function(){
		uni.showLoading({
			title: '加载中...',
			mask: false
		});
		uni.request({
			url: 'https://unidemo.dcloud.net.cn/api/news',
			method: 'GET',
			data: {},
			success: res => {
				console.log(res);
				this.news=res.data;
				uni.hideLoading();
			},
			fail: () => {},
			complete: () => {}
		});
	},
	methods: {
		openinfo(e) {
			var newid = e.currentTarget.dataset.newid;
			//console.log(newid);
			uni.navigateTo({
				url: '../info/info?newid='+newid
			});
		}
	},
}
